.site-navigation {
  position: relative;
  min-height: 21px;
  width: auto;
}

.site-navigation__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 50px;
  height: 50px;
  background-color: @font-blue;
  cursor: pointer;
  border: none;

  @media (min-width: @desktop-width){
    display: none;
  }
}

.main-nav--closed .site-navigation__toggle::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 17px;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
}

.main-nav--closed .site-navigation__toggle:active::before {
  background-color: fade(#ffffff, 30%);
  box-shadow:
    0 8px 0 0 fade(#ffffff, 30%),
    0 16px 0 0 fade(#ffffff, 30%);
}

@media (max-width: @tablet-width) {
  .main-nav--closed .site-navigation__item {
    display: none;
    &--active {
      display: block;
    }
  }

  .main-nav--nojs .site-navigation__toggle {
    display: none;
  }

  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }

  .main-nav--nojs .site-list__item {
    display: block;
  }

  .main-nav--nojs .site-list__item--active a {
    padding-right: 21px;
    padding-left: 21px;
    text-align: left;
  }

  .main-nav--nojs .user-list__item {
    display: block;
    background-color: #6a4d38;
  }
}

.main-nav--opened .site-navigation__toggle {
  top: 23px;
  right: 23px;
  left: auto;
  @media (min-width: @tablet-width) {
    position: absolute;
    top: 46.75px;
  left: 617px;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 23px;
    left: 18px;
    width: 23px;
    height: 2px;
    background-color: #ffffff

  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    background-color: @red;
  }
}
.site-navigation--string {
  @media (min-width: @tablet-width) {
    min-height: 39px;
  }
}

.site-navigation__list a {
  text-decoration: none;
  color: @white;
  text-transform: uppercase;

  @media (min-width: @desktop-width) {
    display: block;
    padding: 20px 20px;
    text-transform: none;
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
  }
}

.site-navigation__item {
  font-weight: bold;
  font-size: 18px;
  line-height: 32px;
  color: @white;
  text-align: center;
  box-shadow: inset 0 2px 0 @nav-shadow,
    0 1px 0 @black;
  padding-top: 15px;
  padding-bottom: 15px;

  @media (min-width: @desktop-width) {
    box-shadow: none;
    display: block;
    padding: 20px 20px;
    text-transform: none;
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
  }
}

.site-navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
}


.site-navigation a:hover,
.site-navigation a:focus {
  color: @red;
}

.site-navigation__wrapper {
  display: flex;
  flex-direction: column;
  background-color: @font-blue;
  width: 320px;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    min-height: 52.31px;
    width: 660px;
    margin: 0 auto;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 960px;
    margin: 0 auto;
  }
}
